<template>
  <div class="base-info-box">
    <h4>基本信息</h4>
    <a-form class="base-info-form" :form="form" layout="inline">
      <a-form-item
        v-if="formData.technologyCode!=''"
        label="工艺路线代码"
        :label-col="labelCol"
        :wrapper-col="wrapperCol"
      >
        <a-input
          disabled
          v-decorator="['technologyCode',{rules: [{ required: true, message: '请输入工艺路线代码' }]}]"
          placeholder="请输入工艺路线代码"
        />
      </a-form-item>
      <a-form-item label="工艺路线名称" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input
          v-decorator="['technologyName',{rules: [{ required: true, message: '请输入工艺路线名称' }]}]"
          placeholder="请输入工艺路线名称"
        />
      </a-form-item>
      <a-form-item label="工艺路线类型" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input v-decorator="['technologyType']" placeholder="请输入工艺路线类型" />
      </a-form-item>
      <a-form-item label="状态" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input v-decorator="['technologyState']" placeholder="请输入状态" />
      </a-form-item>
      <a-form-item label="零部件编码" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-select
          v-decorator="['productCode',{rules: [{ required: true, message: '请输入零部件编码' }]}]"
          placeholder="请输入零部件编码"
          @change="changeProductCode"
          showSearch
          :filterOption="false"
          :defaultActiveFirstOption="false"
          @search="search"
        >
          <a-select-option v-for="(v,i) in productList" :key="v.productCode">{{v.productCode}}</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="零部件名称" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input disabled v-decorator="['productName']" placeholder="零部件名称" />
      </a-form-item>
      <a-form-item label="零部件分类" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-input disabled v-decorator="['claName']" placeholder="零部件分类" />
      </a-form-item>
      <a-form-item label="备注" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-textarea disabled v-decorator="['productDescribe']" placeholder="请输入备注" :rows="4" />
      </a-form-item>
    </a-form>
  </div>
</template>
<script>
import { getCraftDetail, searchProduct } from "@/api/craft";
export default {
  props: {
    id: {
      type: Number,
      default: null
    },
    data: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      form: this.$form.createForm(this),
      labelCol: { span: 6 },
      wrapperCol: { span: 14 },
      formData: {
        detail:false,
        claName: "",
        delProductTechnologyProcessList: null,
        productCode: "",
        productDescribe: "",
        productName: "",
        productTechnologyProcessList: null,
        technologyCode: "",
        technologyId: "",
        technologyName: "",
        technologyState: "",
        technologyType: ""
      },
      productList: []
    };
  },
  mounted() {
    if (this.id != null) {
      Object.assign(this.formData, this.data);
      let {
        technologyCode,
        technologyName,
        technologyType,
        technologyState,
        productCode,
        productName,
        claName,
        productDescribe
      } = this.formData;
      setTimeout(() => {
        this.form.setFieldsValue({
          technologyCode,
          technologyName,
          technologyType,
          technologyState,
          productCode,
          productName,
          claName,
          productDescribe
        });
      }, 0);
    }
  },
  methods: {
    async search(key) {
      let res = await searchProduct(key);
      if (res.data.code != this.$global.successCode) return;
      this.productList = res.data.data;
    },
    changeProductCode(v) {
      let {
        productCode,
        productName,
        claName,
        productDescribe
      } = this.productList.find(m => m.productCode == v);
      this.form.setFieldsValue({
        productCode,
        productName,
        claName,
        productDescribe
      });
    }
  }
};
</script>
<style lang="less">
.base-info-box {
  background-color: #fff;
  padding: 10px;
  border-radius: 4px;
  margin-left: 20px;
  margin-right: 20px;
  > h4 {
    font-weight: bold;
    border-bottom: 1px solid #e4e4e4;
    padding: 10px 0;
  }
  .base-info-form {
    margin: 0 50px;
    .ant-form-item {
      width: 50%;
      margin: 10px 0;
    }
    .ant-form-item-required::before {
      font-weight: bold;
    }
  }
}
</style>