<template>
  <div class="table-box">
    <a-table
      class="table"
      :pagination="{showSizeChanger:true,showQuickJumper:true,total, current,pageSize,showTotal:total => `共 ${total} 条记录`}"
      @change="onchange"
      :rowKey="(v,i)=>i"
      :columns="table"
      :dataSource="tableData"
    >
      <template slot="technologyCode" slot-scope="text, v,index">
        <l-link @click="$emit('addTab',`工艺路线${v.technologyId}-详情`,v.technologyId,{...v,detail:true})">{{text}}</l-link>
      </template>
      <template slot="operation" slot-scope="text,v,i">
        <l-link @click="$emit('addTab',`工艺路线${v.technologyId}-详情`,v.technologyId,v)">编辑</l-link>
        <l-link @click="operation(v)">删除</l-link>
      </template>
    </a-table>
  </div>
</template>
<script>
import { getCraft, updateCraft, deleteCraft } from "@/api/craft";
export default {
  props: {
    code: {
      default: ""
    },
    name: {
      default: ""
    }
  },
  data() {
    return {
      table: [
        {
          title: "工艺路线代码",
          dataIndex: "technologyCode",
          scopedSlots: { customRender: "technologyCode" }
        },
        {
          title: "工艺路线名称",
          dataIndex: "technologyName",
          scopedSlots: { customRender: "technologyName" }
        },
        {
          title: "零部件分类",
          dataIndex: "claName",
          scopedSlots: { customRender: "claName" }
        },
        {
          title: "零部件编码",
          dataIndex: "productCode",
          scopedSlots: { customRender: "productCode" }
        },
        {
          title: "零部件名称",
          dataIndex: "productName",
          scopedSlots: { customRender: "productName" }
        },
        {
          title: "零部件描述",
          dataIndex: "productDescribe",
          scopedSlots: { customRender: "productDescribe" }
        },
        {
          title: "工艺路线类型",
          dataIndex: "technologyType",
          scopedSlots: { customRender: "technologyType" }
        },
        {
          title: "状态",
          dataIndex: "technologyState",
          scopedSlots: { customRender: "technologyState" }
        },
        {
          title: "操作",
          dataIndex: "operation",
          scopedSlots: { customRender: "operation" }
        }
      ],
      tableData: [],
      total: 0,
      pageSize: 10,
      current: 1
    };
  },
  mounted() {
    this.getTable();
  },
  methods: {
    async getTable(current = this.current) {
      let { pageSize, code, name } = this;
      let res = await getCraft({
        page: current,
        pageSize,
        technologyCode: code,
        technologyName: name
      });
      if (res.data.code != this.$global.successCode) return;
      this.current = current;
      this.total = res.data.data.total;
      this.tableData = res.data.data.list;
    },
    onchange(v) {
      let { current, pageSize } = v;
      Object.assign(this, { current, pageSize });
      this.getTable();
    },
    async operation(v) {
      let res = await deleteCraft(v.technologyId);
      if (res.data.code != this.$global.successCode) return;
      this.getTable();
    }
  }
};
</script>
<style lang="less" scoped>
.table-box {
  height: 100%;
  width: 100%;
  overflow-x: auto;
  .table {
    input {
      width: 120px;
    }
  }
}
</style>