<template>
  <div class="box">
    <div class="header">
      <h2>{{data.detail?'':id?'编辑':'添加'}}工艺路线{{data.detail?'详情':''}}</h2>
      <a-button @click="finish" type="primary">{{id?'保存':'完成'}}</a-button>
    </div>
    <baseinfo ref="baseinfo" :id="id" :data="data"></baseinfo>
    <div class="craft-info">
      <h4>{{id?'编辑':'添加'}}工序信息</h4>
      <a-button type="primary" @click="$refs.addinfo.visible=true">+ 添加</a-button>
      <infotable ref="infotable" class="table"></infotable>
    </div>
    <addinfo ref="addinfo" @getTable="$refs.infotable.getTable()"></addinfo>
  </div>
</template>
<script>
import baseinfo from "./baseinfo";
import addinfo from "./addinfo";
import infotable from "./infotable";
import { addCraft, updateCraft } from "@/api/craft";
export default {
  props: {
    id: {
      type: Number,
      default: null
    },
    data: {
      type: Object,
      default: () => {}
    }
  },
  components: {
    baseinfo,
    infotable,
    addinfo
  },
  data() {
    return {
      breadcrumb: ["首页", "云MDM", "工艺路线"]
    };
  },
  methods: {
    finish() {
      let form = this.$refs.baseinfo.form;
      form.validateFields({ first: true }, async valid => {
        if (valid) return;
        let value = form.getFieldsValue();
        value = Object.assign(this.$refs.baseinfo.formData,value);
        let method = this.id == null ? addCraft : updateCraft;
        let res = await method(value);
        if (res.data.code != this.$global.successCode) return;
        this.$emit("closeTab");
        this.$emit("getTable");
        form.resetFields();
      });
    }
  }
};
</script>
<style lang="less" scoped>
.box {
  padding-bottom: 10px;
  box-sizing: border-box;
  height: 100%;
  background-color: #f0f2f5;
  overflow-y: auto;
  .header {
    background-color: #fff;
    padding: 10px 20px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    h2 {
      font-weight: bold;
    }
  }
  .craft-info {
    margin-top: 20px;
    background-color: #fff;
    padding: 10px;
    border-radius: 4px;
    margin-left: 20px;
    margin-right: 20px;
    > h4 {
      font-weight: bold;
      border-bottom: 1px solid #e4e4e4;
      padding: 10px 0;
      margin-bottom: 20px;
    }
    .table {
      margin-top: 20px;
    }
  }
}
</style>