<template>
  <div class="craft-box">
    <a-tabs hideAdd v-model="activeKey" type="editable-card" @edit="onEdit" class="tabtilt">
      <a-tab-pane v-for="(pane,i) in panes" :tab="pane.title" :key="i" :closable="!pane.close">
        <route ref="route" v-if="pane.type==0" @addTab="addTab"></route>
        <addRoute @closeTab="remove(i)" @getTable="getTable" v-else-if="pane.type==2" :id="Number(pane.id)" :data="pane.data"></addRoute>
      </a-tab-pane>
    </a-tabs>
  </div>
</template>
<script>
import route from "./route";
import addRoute from "./addRoute";
export default {
  components: {
    route,
    addRoute
  },
  data() {
    return {
      activeKey: 0,
      panes: [{ title: "工艺路线", type: 0, close: true }]
    };
  },
  mounted() {},
  methods: {
    addTab(title = "", id = null, data, type = 2) {
      if (this.panes.some(v => id == v.id && type == v.type)) {
        this.activeKey = this.panes.findIndex(v => id == v.id);
      } else {
        this.panes.push({
          title,
          id,
          data,
          type
        });
        this.activeKey = this.panes.length - 1;
      }
    },
    onEdit(targetKey, action) {
      if (action == "remove") this[action](targetKey);
    },
    remove(targetKey) {
      let { panes, activeKey } = this;
      this.panes = panes.filter((v, i) => i != targetKey);
      this.activeKey = 0;
    },
    getTable() {
      this.$refs.route[0].$refs.routetable.getTable(1);
    }
  }
};
</script>
<style lang="less">
.craft-box {
  height: 100vh;
  overflow: hidden;
  min-width: 1100px;
  .ant-tabs {
    height: 100%;
    padding-top: 50px;
    .ant-tabs-bar {
      padding-top: 10px;
      margin-top: -50px;
      margin-bottom: 0;
    }
    .ant-tabs-content,
    .ant-tabs-tabpane {
      height: 100%;
    }
  }
}
</style>
